@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {

  :root {
    --background: 0 0% 100%;
    --foreground: 240 10% 3.9%;
    --card: 0 0% 100%;
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 240 5.9% 10%;
    --primary-foreground: 0 0% 98%;
    --secondary: 240 4.8% 95.9%;
    --secondary-foreground: 240 5.9% 10%;
    --muted: 240 4.8% 95.9%;
    --muted-foreground: 240 3.8% 46.1%;
    --accent: 240 4.8% 95.9%;
    --accent-foreground: 240 5.9% 10%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 5.9% 90%;
    --input: 240 5.9% 90%;
    --ring: 240 10% 3.9%;
    --chart-1: 12 76% 61%;
    --chart-2: 173 58% 39%;
    --chart-3: 197 37% 24%;
    --chart-4: 43 74% 66%;
    --chart-5: 27 87% 67%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 240 10% 3.9%;
    --foreground: 0 0% 98%;
    --card: 240 10% 3.9%;
    --card-foreground: 0 0% 98%;
    --popover: 240 10% 3.9%;
    --popover-foreground: 0 0% 98%;
    --primary: 0 0% 98%;
    --primary-foreground: 240 5.9% 10%;
    --secondary: 240 3.7% 15.9%;
    --secondary-foreground: 0 0% 98%;
    --muted: 240 3.7% 15.9%;
    --muted-foreground: 240 5% 64.9%;
    --accent: 240 3.7% 15.9%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 3.7% 15.9%;
    --input: 240 3.7% 15.9%;
    --ring: 240 4.9% 83.9%;
    --chart-1: 220 70% 50%;
    --chart-2: 160 60% 45%;
    --chart-3: 30 80% 55%;
    --chart-4: 280 65% 60%;
    --chart-5: 340 75% 55%;
  }

  /* âœ… Remove number input spinner arrows */
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield; /* Firefox */
  }
  input:-webkit-autofill {
    background-color: transparent !important;
  }
}



button {
    @apply bg-transparent shadow-none outline-none;
    background-color: transparent !important;
    border: none;
    cursor: pointer;
  }

  


.truncate-cell {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%; /* Ensures it fits within its column */
  display: block;
}

/* ------------- Date Picker (Selected Date) ------------- */
.MuiPickersDay-root.Mui-selected {
  background-color: #7902DF !important; /* Purple */
  color: white !important;
}

/* Hover effect for unselected dates */
.MuiPickersDay-root:hover {
  background-color: #a352df !important; /* Light Purple */
}

/* Default color for unselected dates */
.MuiButtonBase-root.MuiPickersDay-root:not(.Mui-selected) {
  color: #333 !important;
}

/* ------------- Time Picker (Dropdown) ------------- */
/* Selected hour, minutes, AM/PM in dropdown */
.MuiClock-root .Mui-selected,
.MuiClockNumber-root.Mui-selected,
.MuiPickersTimeClock-root .Mui-selected,
.MuiTimeClock-root .Mui-selected {
  background-color: #7902DF !important;
  color: white !important;
}

/* Hover effect for time picker items */
.MuiClock-root .MuiButtonBase-root:hover,
.MuiPickersTimeClock-root .MuiButtonBase-root:hover,
.MuiTimeClock-root .MuiButtonBase-root:hover {
  background-color: #a352df !important;
}

/* Selected AM/PM Button */
.MuiPickersToolbar-root .Mui-selected {
  background-color: #7902DF !important;
  color: white !important;
}

/* ------------- OK & CANCEL Buttons ------------- */
.MuiDialogActions-root button {
  color: #7902DF !important;
}

/* Time picker scrollbar track */
.MuiTimeClock-root::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

/* Time picker scrollbar thumb */
.MuiTimeClock-root::-webkit-scrollbar-thumb {
  background-color: #7902DF !important;
}
